  font-weight: 900;
  @include animation(secfadein 2s);

  color: white;

  a {
    color: white;
    &:hover {
        color: white;
    }
  }

  .unit {
    display: block;
    padding: 10px 0 0px;
    &:hover {
        background-color: lighten(#558C89, 5%);
    }
    .titlehead {
    // background: #fff;
      padding: 40px 20px 50px;
      text-align: left;
      text-transform: none;
      font-size: 50px;
      .codesym {
        color: #e1e1e1;
        font-size: 60px;
        padding: 0 2px;
        position: relative;
        top: 2px;
      }
      .ifbody {
        font-size: 31px;
        padding-left: 72px;
      }
    }
  }

