/* Ingredients */

.ingredients {
  background-color: steelblue;
  font-weight: 900;
  text-align: center;
  border: 5px solid white;
  @include animation(secfadein 2s);

  color: white;

  .grid {
    display: table;
    width: 100%;
    .row {
      display: table-row;
      a {
        color: white;
        &:hover {
            color: white;
        }
      }
      .tblunit1 {
        font-size: 1.30em;
        display: table-cell;
        padding: 10px 0 0px;
        border-style: solid;
        border-color: white;
        border-width: 0px 2px;
        &:hover {
            background-color: lighten(steelblue, 5%);
        }
      }
      .tblunit2 {
        display: table-cell;
        padding: 10px 0 0px;
        border-style: solid;
        border-color: white;
        border-width: 0px 2px;
      }
    }
  }

  h2 {
    margin: 1em;
  }

  p {
    font-size: 1.2em;
    margin: 1em;
  }

}

.example {
  position: relative;
  border: 1px solid;
  margin: 10px;
  .title {
    $bg_color: #616D71;
    position: absolute;
    top: 0px;
    right: 0px;
    opacity: 0.8;
    border-radius: 5px;
    margin: 5px;
    padding: 0.3em;
    background-color: $bg_color;
    &:hover {
        background-color: lighten($bg_color, 5%);
    }
    font-weight: bold;
    font-size: 1.0em;
    color: white;

  }
  .code {
    display: block;
    top: 0px;
    div pre {
      width: 100%; // workaround
    }
    .tooltip {
      position: relative;
      text-decoration: underline;
      text-decoration-color: grey;
      text-decoration-style: wavy;
      cursor: pointer;
      //Tooltip text container
      &::before {
        position: absolute;
        top: -1.8em;
        left: -0em;
        background-color: DarkSlateGrey;
        border-radius: 5px;
        color: #fff;
        font-family: Menlo, Consolas, Monaco, monospace;
        font-style: normal;
        content: attr(data-tooltip); //This pulls in the text from the element with the tooltip
        padding: 0.2em;
        text-transform: none;
        transition: all 0.5s ease;
      }

      //Tooltip arrow
      &::after {
        position: absolute;
        top: -0.2em;
        left: 9px;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        border-top: 5px solid DarkSlateGrey;
        content: " ";
        font-size: 0;
        line-height: 0;
        margin-left: -5px;
        width: 0;
      }

      //Setting up the transition
      &::before,
      &::after {
        color: #efefef;
        font-family: monospace;
        font-size: 16px;
        opacity: 0;
        pointer-events: none;
        text-align: center;
      }

      //Triggering the transition
      &:focus::before,
      &:focus::after,
      &:hover::before,
      &:hover::after {
        opacity: 1;
        transition: all 0.75s ease;
      }
    }
  }
}

@media (min-width: 569px) {
  .ingredients p { font-size: 1.2em; }
}
/* Quickstart */

.quickstart {
  background-color: #3F1F1F;
  color: #fff;
  margin: 60px 0;
  @include box-shadow(inset 0 3px 10px rgba(0,0,0,.4));

  .content { padding: 0; }

  h3 {
    font-size: 24px;
    line-height: 24px;
    margin-top: 20px;
    text-shadow: 0 1px 3px rgba(0,0,0,.8);
  }

  .code {
    font-size: 12px;
    display: block;
    margin: 0 0 -30px;
  }
}

@media (min-width: 768px) {
  .quickstart {

    .code {
      font-size: 18px;
      margin: -30px 0;
      float: right;
    }

    h3 {
      margin: 50px 0 0;
      text-align: center;
    }
  }
}

/* Code */

.quickstart {

  .code {
    display: block;
    padding: 0;
    font-family: Menlo, Consolas, "Courier New", Courier, "Liberation Mono", monospace;
    line-height: 1.3em;

    .title {
      display: block;
      text-align: center;
      margin: 0 20px;
      padding: 5px 0;
      @include border-radius(5px 5px 0 0);
      @include box-shadow(0 3px 10px rgba(0,0,0,.5));
      font: 400 16px/24px 'Helvetica Neue', Helvetica, Arial, sans-serif;
      color: #444;
      text-shadow: 0 1px 0 rgba(255,255,255,.5);
      background-color: #f7f7f7;
      background-image: url(data:image/svg+xml;base64,);
      background-image: -webkit-gradient(linear, left top, left bottom, from(#f7f7f7), color-stop(7%, #cfcfcf), to(#aaaaaa));
      background-image: -webkit-linear-gradient(top, #f7f7f7 0%, #cfcfcf 7%, #aaaaaa 100%);
      background-image: -moz-linear-gradient(top, #f7f7f7 0%, #cfcfcf 7%, #aaaaaa 100%);
      background-image: -o-linear-gradient(top, #f7f7f7 0%, #cfcfcf 7%, #aaaaaa 100%);
      background-image: linear-gradient(top, #f7f7f7 0%,#cfcfcf 7%,#aaaaaa 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f7f7f7', endColorstr='#aaaaaa',GradientType=0 );
      border-bottom: 1px solid #111;
    }

    .shell {
      padding: 20px;
      text-shadow: none;
      margin: 0 20px;
      background-color: #171717;
      @include border-radius(0 0 5px 5px);
      @include box-shadow(0 5px 30px rgba(0,0,0,.3));
    }

    .line {
      display: block;
      margin: 0;
      padding: 0;

      span { display: inline-block; }
    }

    .path {
      color: #87ceeb;
      @include user-select(none);
    }

    .prompt {
      color: #cd5c5c;
      -webkit-user-select: none;  /* Chrome all / Safari all */
      -moz-user-select: none;     /* Firefox all */
      -ms-user-select: none;      /* IE 10+ */
      -o-user-select: none;
      user-select: none;
    }

    .command { color: #f0e68c; }

    .output { color: #888; }
  }
}

@media (min-width: 768px) {
